@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800&display=swap);
.project__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.project__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.project__navbar-links_logo {
    margin-right: 2rem;
}

.project__navbar-links_logo img{
    width: 60px;
    height: 30px;
}

.project__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.project__navbar-links_container p, 
.project__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.project__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.project__navbar-menu svg {
    cursor: pointer;
}

.project__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.project__navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    .project__navbar-links_container {
        display: none;
    }

    .project__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .project__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .project__navbar {
        padding: 2rem;
    }

    .project__navbar-menu_container {
        top: 20px;
    }
}

.project__repo-container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 2rem;
    margin: 2rem;
    border-radius: 1rem;
    background: var(--gradient-bar)
}

.project__repo-container h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;

}

.project__repo-container p { 
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #fff;
}
.project__highlights-container__highlight {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.project__highlights-container__highlight-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.project__highlights-container__highlight-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.project__highlights-container__highlight-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.project__highlights-container__highlight-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
    
}

.project__highlights-container__highlight-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .project__highlights-container__highlight-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .project__highlights-container__highlight-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .project__highlights-container__highlight {
        margin: 1rem 0;
    }
}
.project__header {
    display: flex;
}

.project__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 6rem;
}

.project__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}

.project__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;

    color: var(--color-text);
    margin-top: 1.5rem;
}
.project__footer {
    display: felx;
    flex-direction: column;
    justify-content: center;
    height: 4rem;

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 4% 30%, rgba(65, 48, 153, 1) 0%, rgba(18, 33, 43, 1) 100%);
}
.project__projects {
    display: felx;
    flex-direction: column;
    margin-left: 2rem;
    margin-right: 2rem;
}

.project__projects-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 1rem;
}

.project__projects-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 52px;
    letter-spacing: -0.04em;
    margin-left: 2rem;
}

.project__projects-container_group {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0rem;
}

@media screen and (max-width: 990px) {
    .project__projects-container_group {
        grid-template-columns: repeat(2, 1fr);
        margin: 2rem 0;
    }

    .project__projects-container_group .project__projects-container_repo {
        width: 50%;
    }
}

@media screen and (max-width: 700px) {
    .project__projects-container_group {
        grid-template-columns: repeat(1, 1fr);        
    }

    .project__projects-container_group .project__projects-container_repo {
        width: 50%;
    }
 }
.project__skills {
    display: flex;
    flex-direction: column;
    padding: 2rem;

    
    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 4% 30%, rgba(65, 48, 153, 1) 0%, rgba(18, 33, 43, 1) 100%);

}

.project__skills-highlight {
    display: flex;
}

.project__skills-highlight .project__highlights-container__highlight {
    margin: 0;
}


.project__skills-highlight .project__highlights-container__highlight-text {
    max-width: 700px;
}

.project__skills-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}

.project__skills-heading h1 {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 510px;
}

.project__skills-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
}


.project__skills-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
    
}

.project__skills-container .project__highlights-container__highlight {
    flex: 1 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;

}

.project__skills-container .project__highlights-container__highlight-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .project__skills-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .project__skills-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .project__skills-highlight .project__highlights-container__highlight {
        flex-direction: column;
    }

    .project__skills-highlight .project__highlights-container__highlight-text {
        margin-top: 0.5rem;
    }

    .project__skills-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .project__skills-highlight .project__highlights-container__highlight {
        margin: 1rem 0;
        min-width: 100%;
    }
}
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background-color: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 4% 30%, rgba(65, 48, 153, 1) 0%, rgba(18, 33, 43, 1) 100%);
}

.gradient__text{
    background:var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6 rem;
}

.section__margin {
    margin: 4rem 6rem;
}

